body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.goBack {
  min-width: 200px;
  background: #009688;
  border: 1px solid #004D40;
  font-size: 1.2em;
  margin: 0 auto;
}

.goBack:hover, .goBack:focus {
  background: #078073;
  border: 1px solid #009688;
}

.button-anth {
  /* margin: 0 auto; */
  width: 100%;
  margin-bottom: 0.7em;
}

/* INTRO */
.intro {
  text-align: justify;
  font-size: 1.2em;
  padding: 1.5em;
}

/* ICONS Font-aweson */
i {
  font-size: 1.5em;
  padding-top: 0.2em;
}

.trashImg {
  font-size: 1.2em;
}

/* Tooltip */
.ant-tooltip-placement-top .ant-tooltip-arrow, .ant-tooltip-placement-topLeft .ant-tooltip-arrow, .ant-tooltip-placement-topRight .ant-tooltip-arrow {
  bottom: 3px;
  border-width: 5px 5px 0;
  border-top-color: #009688;
}

.ant-tooltip-inner  {
  background-color: #009688;
}

/* COLLAPSE */

.ant-collapse > .ant-collapse-item > .ant-collapse-header > div{
  display: flex;
  flex: 1 1 100%;
  max-width: 100%;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header > div > span {
  width: 100%;
}

.ant-collapse > .ant-collapse-item > .ant-collapse-header > div > button > span{
  /* align-content: flex-end; */
}

/* ICONS */
.fas .fa-check {
  color: #009688;
}


.App {
  background: #FAFAFA;
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
  width: 80%;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  padding: 1.3em;
  margin: 0 auto;
  min-height: 100vh;
}

/* 
  LOADING
*/
.App #load {
  margin: 2em auto;
}

/*
  CONNEXION
*/

.connexion {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/*
    FORMULAIRE
*/
.form {
  background: #009688;
  border: 1px solid #004D40;
  border-width: 0 0 3px 0;
  border-radius: 50px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  display: flex;
  font-size: 1.5em;
  height: 2.5em;
  margin-bottom: 0.5em;
}

.form > * {
  background: #009688;
  border: none;
  color: #fff;
  font-family: 'Roboto', sans-serif; 
  outline: none;
  padding: 1em;
}

.form-titre {
  background: #078073;
  margin: 1em auto;
  width: 100%;
  border-radius: 50px;
}

.form-titre > * {
  background: #078073;
  border-radius: 50px;
}

.form > input {
  width: 100%;
  font-size: 0.9em;
  
}
.form > input::-webkit-input-placeholder {
  color: #B2DFDB;
}
.form > input::-ms-input-placeholder {
  color: #B2DFDB;
}
.form > input::placeholder {
  color: #B2DFDB;
}

.form-comm {
  width: 100%;
}

.form-comm > input {
  box-sizing: border-box;
  width: 100%;
  font-size: 0.9em;
  padding: 0.6em;
}

/*
  BOUTONS
*/

button {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  width: 50px;
}

.form > button {
  -webkit-transition: 0.5s;
  transition: 0.5s;
  font-size: 0.9em;
  padding: 0;
}

#buttonSubmit {
  border-radius: 50%;
}

.formButton-active {
  background: #26A69A;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

/* SWITCH */
.ant-switch, .ant-switch-checked {
  border: 1px solid #078073 ;
  font-size: 14px;
  line-height: 1.5;
  color: rgba(0, 0, 0, 0.65);
  margin: auto 0 auto 0.5em;
  padding: 0;
  list-style: none;
  position: relative;
  display: inline-block;
  box-sizing: border-box;
  height: 24px;
  min-width: 44px;
  line-height: 20px;
  vertical-align: middle;
  /* border-radius: 5px; */
  background-color: rgba(0, 0, 0, 0.25);
  cursor: pointer;
  -webkit-transition: all 0.36s;
  transition: all 0.36s;
  align-content: center;
}

/*
  ITEMS
*/

.list-items {
  font-size: 1.1em;
  padding: 0;
  -webkit-perspective: 500px;
          perspective: 500px;
}

.item-container {
  /* opacity: 0.01; */
  background: #fff;
  display: flex;
  max-width: 100%;
  padding: 0;
  -webkit-transition: 0.6s;
  transition: 0.6s;
  flex-direction: column;
}

h2 {
  margin: 0 auto;
  text-align: center;
}

.item-container h3 {
  margin: 1em auto;
}

.item-container input {
  border: none;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  background: rgb(243, 238, 238);
  color: rgb(83, 76, 76);
}

.item {
  background: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  border-radius: 8px;
  display: flex;
  flex: 1 1 100%;
  max-width: 100%;
  padding: 10px;
  -webkit-transition: all 1s;
  transition: all 1s;
}

.item:hover {
  box-shadow: 3px 3px 5px #26A69A;
}

.item-menu {
  margin-bottom: 1em;
}

.item-list {
  border-radius: 8px;
  background: #ECEFF1;
}

.item-list:hover {
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
}

.item span:nth-child(1) {
  width: 100%;
  /* margin-left: 1em; */
}

.item-titre-container {
  padding: 0;
}

.item-titre {
  background: #fff;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  border-radius: 7px;
  display: flex;
  align-content: center;
  width: 100%;
  max-width: 100%;
  padding: 1em 1em 1em 5em;
  margin: 0.7em auto;
  -webkit-transition: 0.6s;
  transition: 0.6s;
}

.item-button {
  width: 100%;
  -webkit-transition: 0.6s;
  transition: 0.6s;
  text-align: center;
  font-size: 1.2em;
  cursor: pointer;
}

.item-button:hover {
  color: #26A69A;;
  font-weight: bold;
}

.checked {
  border-radius: 50px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.21);
  margin-right: 1em;
  font-size: 1.5em;
  background: #FAFAFA;
}

.checked span:nth-child(1)  {
  width: 100%;
  margin-left: 0;
}

.checked:hover {
  box-shadow: 3px 3px 5px #159e90cc;
}

.trash {
  padding: 0;
}

/*
  ANIMATION
*/

.flip {
  background: #ECEFF1;
  color: #9E9E9E;
  -webkit-transform: rotateX(360deg);
          transform: rotateX(360deg);
}
.flip > span {
  text-decoration: line-through;
}

.hide {
  display: none;
}

/*
  FIREBASE-UI
*/

.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #0288D1;
  background: -webkit-radial-gradient(circle, #039BE5, #01579b);
  background: radial-gradient(circle, #039BE5, #01579b);
  z-index: 10000;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  flex-direction: column;
  font-family: sans-serif;
}
.signedIn {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.logo {
  font-family: 'Amaranth', sans-serif;
  font-size: 200%;
}
.logoIcon {
  top: 4px;
  font-size: 32px;
  margin-right: -2px;
  position: relative;
}
.caption {
  margin: 20px 0 40px 0;
  font-family: 'Amaranth', sans-serif;
  font-size: 18px;
  opacity: 0.8;
}
.skip {
  font-weight:lighter;
  color:white;
  opacity: 0.7;
  width: 100%;
  display: block;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
}
.skip:HOVER {
  text-decoration: underline;
}
.firebaseUi {
  min-width: 250px;
}
.button {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
  margin-top: 10px;
}
.message-enter {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: opacity 800ms ease-in;
    transition: opacity 800ms ease-in;
}

.message-enter-active {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: all 0.6s;
    transition: all 0.6s;
    width:auto;
    height:auto;
}

.message-leave {
    opacity: 0.01;
    -webkit-transition: opacity 380ms ease-in;
    transition: opacity 380ms ease-in;
}

.message-leave-active {
    opacity:0;
    -webkit-transition: all 1s;
    transition: all 1s;
    width:0;
    height:0;
    overflow:hidden;
    box-sizing:border-box;
    position:relative;   
    -webkit-transform: translateY(0);   
            transform: translateY(0); 
}

.box {
    opacity: 1;
    -webkit-transition: opacity 1s, height 0;
    transition: opacity 1s, height 0;
}
    
.hidden {
    opacity: 0;
    -webkit-transition: opacity 2s, height 0 2s;
    transition: opacity 2s, height 0 2s;
    height: 0;
}
